@import url("https://fonts.googleapis.com/css2?family=Nunito:wght@200;300;400;600;700&display=swap");

* {
  font-family: "Nunito", sans-serif;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  outline: none;
  border: none;
  text-decoration: none;
  text-transform: capitalize;
}

.container {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  padding: 20px 7%;
  background-color: #eee;
  min-height: 100vh;
  gap: 15px;
  padding-bottom: 80px;
}

.container .content {
  flex: 1 1 410px;
}

.container .image {
  flex: 1 1 400px;
}
.container .image img {
  width: 100%;
}

.container .content .title {
  font-size: 45px;
  text-transform: uppercase;
  color: #2980b9;
}

.container .content p {
  color: #666;
  padding: 5px 0;
}

.container .content .btn {
  display: inline-block;
  padding: 8px 50px;
  margin: 10px 0;
  font-size: 17px;
  background-color: #2980b9;
  color: #fff;
  transition: 0.2s ease-out;
}

.container .content .btn:hover {
  letter-spacing: 2px;
}

@media screen and (max-width: 500px) {
  .container {
    padding: 20px;
  }

  .container .content {
    text-align: center;
  }

  .container .content .title {
    font-size: 35px;
  }
  .container .content p {
    font-size: 14px;
  }
}
